var test = require('ava');
var app = require('../app');
var request = require('supertest-promised')(app);

function connect(t) {
  return request.post('/')
    .send({
      "host": "113.172.82.132",
      "user": "support",
      "pass": "support",
      "tunport": 1080
    })
    .expect(200)
    .expect(function (res) {
      t.is(res.body.status, 'Connected');
    });
}

test.serial('first connection', connect);
test.serial('repetitive connection', connect);
